<template>

        <v-card class="text-left">
            <v-card-title>
                <div class="subtitle-1 font-weight-light text-left ml-3 mt-2"> Consulter un utilisateur</div>
                <v-spacer />
                <v-icon aria-label="Close" @click="handleClose()" >
                    mdi-close
                </v-icon>
            </v-card-title>

                        <v-card-text>
                <v-row>
                    <el-divider></el-divider>
                    <v-col cols="12" md="4" >
                        <v-text-field class="purple-input" v-model="form.nom" label="Nom" disabled/>
                    </v-col>
                    <v-col cols="12" md="4" >
                        <v-text-field class="purple-input" v-model="form.adresse" label="Adresse" disabled/>
                    </v-col>
                    <v-col cols="12" md="4">
                        <el-select v-model="form.ville" placeholder="Ville" class="mt-2" disabled>
                            <el-option v-for="item in villes" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </v-col>
                    <v-col cols="12" md="4">
                        <el-select v-model="form.responsable_id" placeholder="Rsponsable" class="mt-2" disabled>
                            <el-option v-for="item in all_user" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </v-col>
                    <v-col cols="12" md="4">
                        <el-select v-model="form.banques" multiple placeholder="banques" class="mt-2" disabled>
                            <el-option v-for="item in all_banque" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </v-col>
                </v-row>
            </v-card-text>

        </v-card>
    
</template>
<script>

    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';
    import axios from 'axios';
    import moment from 'moment';

    export default {

        components: {
        },

        props: ['handleClose','form'],

        data() {

            return {

                villes: [
                    { value: 'Yaound', label: 'Yaound'},
                    { value: 'Douala', label: 'Douala'},
                    { value: 'Akonolinga', label: 'Akonolinga'},
                    { value: 'Abong Mbang', label: 'Abong Mbang'},
                    { value: 'Bali', label: 'Bali'},
                    { value: 'Banyo', label: 'Banyo'},
                    { value: 'Bangangt', label: 'Bangangt'},
                    { value: 'Batouri', label: 'Batouri'},
                    { value: 'Blabo', label: 'Blabo'},
                    { value: 'Bertoua', label: 'Bertoua'},
                    { value: 'Bafang', label: 'Bafang'},
                    { value: 'Bua', label: 'Bua'},
                    { value: 'Bafia', label: 'Bafia'},
                    { value: 'Bamenda', label: 'Bamenda'},
                    { value: 'Bafoussam', label: 'Bafoussam'},
                    { value: 'Dschang', label: 'Dschang'},
                    { value: 'Eda', label: 'Eda'},
                    { value: 'Ebolowa', label: 'Ebolowa'},
                    { value: 'Foumban', label: 'Foumban'},
                    { value: 'Nkongsamba', label: 'Nkongsamba'},
                    { value: 'Loum', label: 'Loum'},
                    { value: 'Garoua', label: 'Garoua'},
                    { value: 'Guider', label: 'Guider'},
                    { value: 'Ngaoundr', label: 'Ngaoundr'},
                    { value: 'Koussri', label: 'Koussri'},
                    { value: 'kal', label: 'kal'},
                    { value: 'Kumba', label: 'Kumba'},
                    { value: 'Mbouda', label: 'Mbouda'},
                    { value: 'Maroua', label: 'Maroua'},
                    { value: 'Mbalmayo', label: 'Mbalmayo'},
                    { value: 'Mokolo', label: 'Mokolo'},
                    { value: 'Meiganga', label: 'Meiganga'},
                    { value: 'Mbandjock', label: 'Mbandjock'},
                    { value: 'Nanga Eboko', label: 'Nanga Eboko'},
                    { value: 'Mamf', label: 'Mamf'},
                    { value: 'Melong', label: 'Melong'},
                    { value: 'Mbanga', label: 'Mbanga'},
                    { value: 'Nkambe', label: 'Nkambe'},
                    { value: 'Manjo', label: 'Manjo'},
                    { value: 'Mora', label: 'Mora'},
                    { value: 'Wum', label: 'Wum'},
                    { value: 'Yagoua', label: 'Yagoua'},
                    { value: 'Foumbot', label: 'Foumbot'},
                    { value: 'Limb', label: 'Limb'},
                    { value: 'Kribi', label: 'Kribi'},
                    { value: 'Kumbo', label: 'Kumbo'},
                    { value: 'Obala', label: 'Obala'},
                    { value: 'Sangmlima', label: 'Sangmlima'},
                    { value: 'Tibati', label: 'Tibati'},
                    { value: 'Tiko', label: 'Tiko'},
                    { value: 'Yokadouma', label: 'Yokadouma'},

                ],
                all_user: [],
                all_banque: [],

            }
        },
        
        mounted(){
            this.sexe = this.form.sexe;
        },

        created(){
            this.loadUser();
            this.loadBanque();
        },

        methods: {

            async loadUser()
            {
                NProgress.start();
                try {
                
                    let response = await axios.get('xperf/AcceuilColab');
                    response = response.data.data_T_colab;

                    response.forEach(element => {

                        let user = {
                            "label": element.name,
                            "value": element.id
                        }

                        this.all_user.push(user);
                    });

                } 
                catch (error) {
                    this.$notify({
                        type: 'error',
                        title: "Echec de recuperation"
                    });
                }

                NProgress.done();
            },

            async loadBanque()
            {
                NProgress.start();
                try {
                
                    let response = await axios.get('v1/xperf/setting/banque/get');
                    response = response.data;

                    response.forEach(element => {

                        let banque = {
                            "label": element.title,
                            "value": element.id
                        }

                        this.all_banque.push(banque);
                    });

                } 
                catch (error) {
                    this.$notify({
                        type: 'error',
                        title: "Echec de recuperation"
                    });
                }

                NProgress.done();
            },
        }
    }
</script>

<style>
    
    element.style {
        overflow:hiden;
    }
</style>