<template>
<div class=" p-3">

    <div class="d-flex justify-content-between">
        <div class="display-2 font-weight-light mt-2"> Gestion des agences </div>
         <el-button type="primary" icon="el-icon-plus" round  @click="handleAddUser()">Ajouter</el-button>
    </div>

    <el-divider class="mt-2"></el-divider>
    <el-skeleton :rows="20" animated v-if="loading" />
    <div v-else>
         <el-table :data="all_data.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%">
            <el-table-column type="expand">
                <template slot-scope="scope">
                    <el-tag class="mr-2" disable-transitions v-for="item in scope.row.banques" :key="item.id">{{item.title}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="Code" prop="code" sortable> </el-table-column>
            <el-table-column label="Nom" prop="nom" sortable> </el-table-column>
            <el-table-column label="Ville" prop="ville" sortable> </el-table-column>
            <el-table-column label="Adresse" prop="adresse" sortable> </el-table-column>
            <el-table-column align="right" >
                <template slot="header" slot-scope="scope">
                    <el-input  v-model="search"  size="mini" placeholder="Type to search"/>
                </template>
                <template slot-scope="scope">
                    <el-button icon="el-icon-view" @click="handleWatch(scope.$index, scope.row)" circle></el-button>
                    <el-button type="primary" icon="el-icon-edit"  @click="handleEdit(scope.$index, scope.row)" circle></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.$index, scope.row)" circle></el-button>
                </template>
            </el-table-column> 
        </el-table>
 
        <v-dialog v-model="dialog" max-width="700">
            <AddAgenceComponent  v-if="type ==1" v-bind:handleClose="handleClose" v-bind:loadData="loadData"/>
            <EditAgenceComponent  v-if="type ==2" v-bind:handleClose="handleClose" v-bind:form="form" />
            <WatchAgenceComponent v-if="type ==3"  v-bind:handleClose="handleClose" v-bind:loadData="loadData" v-bind:form="form" />
        </v-dialog>

    </div>
</div>
</template>

<script>

    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';
    import axios from 'axios';
    import moment from 'moment';

    import AddAgenceComponent from '@/components/dashboard/Settings/Agence/AddAgenceComponent.vue';
    import EditAgenceComponent from '@/components/dashboard/Settings/Agence/EditAgenceComponent.vue';
    import WatchAgenceComponent from '@/components/dashboard/Settings/Agence/WatchAgenceComponent.vue';

    export default {
        
        components: {
            AddAgenceComponent,
            EditAgenceComponent,
            WatchAgenceComponent,
        },

        data() {
            return {
                all_data: [],
                search: '',
                loading: true,
                dialog: false,
                type: 0,
                form: '',
            }
        },

        created(){
            this.loadData();
        },

        methods: {

            async loadData()
            {
                NProgress.start();

                try {
                
                    const response = await axios.get('v1/xperf/setting/agence/get');

                    this.all_data = response.data;
                    this.loading = false;


                } 
                catch (error) {

                    this.loading = false;
                    this.$notify({
                        type: 'error',
                        title: "Echec de recuperation"
                    });
                }

                NProgress.done();

            },



            handleEdit(index, row) {
                this.form = row;
                this.dialog = true;
                this.type = 2;
            },

            handleWatch(index, row) {
                this.form = row;
                this.dialog = true;
                this.type = 3;
            },

            handleDelete(index, row) {

                this.$confirm("Souhaiter effacer l'utilisateur. Continuer?", 'Warning', {
                confirmButtonText: 'OK',
                cancelButtonText: 'Annuler',
                type: 'warning'
                }).then(() => {
                    this.delete(row);
                }).catch(() => {
                    this.$notify({
                        type: 'info',   
                        message: 'Suppression annule'
                    });          
                });
            },

            handleClose(){
                this.dialog = false;
            },

            handleAddUser(){
                this.dialog = true;
                this.type = 1;
            },

            async delete(user) {

                NProgress.start();
                try {

                    const response = await axios.post('v1/xperf/setting/agence/delete', user);
                    this.loadData();
                    this.$notify({
                        type: 'success',
                        title: 'Suppression',
                        message: 'Utilisateur supprim avec succs'
                    });
                } 
                catch (error) {

                    this.loading = false;
                    this.$notify({
                        type: 'error',
                        title: "Echec de recuperation"
                    });
                }

                NProgress.done();
            },
            
        },
    }
</script>

<style>
    
    element.style {
        overflow:hiden;
    }
</style>